  font-weight: 400;
  text-transform: uppercase; }

.h1 {
  font-size: 18px; }

.h2 {
  font-size: 16px; }

.h3 {
  font-size: 14px; }

.h4 {
  font-size: 12px; }

.h5 {
  font-size: 10px; }

.align-rigth {
